.feedback {
  position: relative;
  background-color: #00b2ff;
}
.feedback::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: block;
  width: 50%;
  height: 100%;
  background: linear-gradient(139deg, rgba(4, 24, 39, 0.5) 0 48.9%, #00b3ff 33.9% 100%);
  background-color: #041827;
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .feedback::before {
    display: none;
    background-color: transparent;
  }
}

.feedback__container {
  position: relative;
  display: flex;
  justify-content: flex-end;
  padding-top: 165px;
  padding-bottom: 239px;
}
.feedback__container h2 {
  margin: 0 0 12px;
  font-size: 36px;
  line-height: 51px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .feedback__container h2 {
    margin-bottom: 7px;
  }
}
@media (max-width: 767px) {
  .feedback__container h2 {
    font-size: 30px;
    line-height: 42.19px;
  }
}
.feedback__container p {
  margin: 0 0 36px;
  padding-left: 5px;
  line-height: 21px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .feedback__container p {
    margin-bottom: 26px;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .feedback__container p {
    margin-bottom: 35px;
    font-size: 14px;
    line-height: 18.82px;
  }
}
@media (max-width: 1023px) {
  .feedback__container {
    display: block;
    padding-top: 45px;
    padding-bottom: 113px;
  }
}
@media (max-width: 767px) {
  .feedback__container {
    max-width: 270px;
    padding-top: 46px;
    padding-bottom: 89px;
  }
}

@media (max-width: 1023px) {
  .feedback__form {
    padding-right: 0;
  }
}

.feedback__image {
  position: absolute;
  top: 245px;
  left: 39px;
  z-index: 1;
  min-width: 510px;
}
@media (max-width: 1920px) {
  .feedback__image {
    top: 246px;
    left: 33px;
    width: 350px;
  }
}
@media (max-width: 1023px) {
  .feedback__image {
    display: none;
  }
}

.feedback__form-container {
  display: grid;
  padding-left: 5px;
  grid-gap: 20px 30px;
  grid-template-columns: 275px 275px;
}
@media (max-width: 1023px) {
  .feedback__form-container {
    padding-left: 0;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .feedback__form-container {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.form {
  color: #ffffff;
}

.form h3 {
  margin-top: 0;
}

.form p {
  margin-top: 0;
  line-height: 22px;
  font-family: "PT Mono", "Arial", sans-serif;
}

.form input,
.form textarea {
  min-width: 275px;
  min-height: 50px;
  line-height: 22px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: rgb(255, 255, 255);
  background-color: #019bdf;
  border: 2px solid transparent;
  border-radius: 3px;
  cursor: text;
}
.form input:hover, .form input:focus,
.form textarea:hover,
.form textarea:focus {
  background-color: #026592;
  box-shadow: 0 0 0 2px #339a9a;
}
.form input:active,
.form textarea:active {
  opacity: 0.5;
}
.form input::placeholder,
.form textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 1023px) {
  .form input,
.form textarea {
    min-width: 320px;
  }
}
@media (max-width: 767px) {
  .form input,
.form textarea {
    min-width: 270px;
  }
}

.form__input--name {
  grid-column: 1/2;
}

.form__input--phone {
  grid-column: 2/3;
}
@media (max-width: 767px) {
  .form__input--phone {
    grid-column: 1/2;
  }
}

.form textarea {
  min-height: 155px;
  grid-column: 1/3;
}
@media (max-width: 767px) {
  .form textarea {
    min-height: 180px;
    grid-column: 1/2;
  }
}

.form__button {
  min-width: 45px;
  padding: 16px 72px 15px 66px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  background-color: #041827;
  border-color: transparent;
  border-radius: 3px;
}
.form__button:hover, .form__button:active {
  color: #192024;
  background-color: #026592;
}
.form__button:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .form__button {
    max-width: 265px;
    padding: 22px 6px 10px 68px;
  }
}
@media (max-width: 767px) {
  .form__button {
    max-width: 270px;
    padding: 16px 6px 10px 70px;
  }
}

.form__checkbox {
  font-size: 14px;
  line-height: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  opacity: 0.8;
}

.form__checkbox span {
  position: absolute;
  margin-left: -25px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
  cursor: pointer;
}
.form__checkbox span:hover, .form__checkbox span:focus {
  border-color: #026592;
}
.form__checkbox span:active {
  opacity: 0.5;
}

.form__checkbox input {
  position: absolute;
  z-index: 10;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.form__checkbox input[type=checkbox]:checked ~ span {
  position: relative;
  width: 7px;
  height: 5px;
  border: 1px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}

.form__checkbox:hover span,
.form__checkbox input[type=checkbox]:focus ~ span {
  background-color: #72a0ca;
  box-shadow: 0 0 0 2px #2c39f2;
}

.form__checkbox--consultation {
  display: flex;
  align-items: center;
  order: 2;
  cursor: pointer;
  gap: 11px;
}
@media (max-width: 1023px) {
  .form__checkbox--consultation {
    margin-top: -10px;
    margin-left: -8px;
  }
}
@media (max-width: 767px) {
  .form__checkbox--consultation {
    margin-top: -24px;
    margin-left: -2px;
  }
}

.form__checkbox--consultation span {
  width: 14px;
  height: 14px;
  margin-bottom: 2px;
  padding: 0;
}

.form__check {
  padding-top: 18px;
  padding-left: 33px;
}

