  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  opacity: 0.7;
}
.footer__navigation-link:hover, .footer__navigation-link:focus {
  color: #019bdf;
}
.footer__navigation-link:active {
  color: #72a0ca;
}

.footer__address-wrapper h4 {
  margin-bottom: 22px;
}

.footer__address {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-style: normal;
}
@media (max-width: 767px) {
  .footer__address {
    padding-bottom: 13px;
  }
}

.footer__location {
  display: block;
  margin-bottom: 5px;
}
@media (max-width: 767px) {
  .footer__location {
    margin-bottom: 15px;
  }
}
.footer__location:hover, .footer__location:focus {
  background-color: #019bdf;
}
.footer__location:active {
  background-color: #72a0ca;
}

.footer__phone {
  padding: 5px 0;
}

.footer__location,
.footer__phone {
  font-size: 16px;
  line-height: 21px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  opacity: 0.7;
}
@media (max-width: 767px) {
  .footer__location,
.footer__phone {
    font-size: 14px;
    line-height: 19px;
  }
}

.footer__btn {
  display: none;
}
@media (max-width: 767px) {
  .footer__btn {
    position: absolute;
    top: 9px;
    right: -11px;
    display: block;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
    user-select: none;
  }
  .footer__btn::before, .footer__btn::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .footer__btn::after {
    transform: rotate(-270deg);
    transition: all 0.5s ease-out;
  }
}

@media (max-width: 767px) {
  .no-js button::before,
::after {
    display: none;
  }
}

@media (max-width: 767px) {
  .footer__btn.is-active {
    cursor: pointer;
  }
  .footer__btn.is-active::after {
    display: none;
  }
}

